import React from "react";
import {Group, PanelHeader, PanelHeaderBack, SimpleCell, Snackbar, Avatar} from "@vkontakte/vkui";
import Icon24Flash from '@vkontakte/icons/dist/24/flash';
import Icon28DoneOutline from '@vkontakte/icons/dist/28/done_outline';
import Icon24Error from '@vkontakte/icons/dist/24/error';
import fire from "../functions/firebase";
import getParams from "../functions/getParams";

const NotifSnack = (p) => {
    let blueBackground = {backgroundColor: 'var(--accent)'};
    let redBackground = {backgroundColor: 'var(--dynamic_red)'};
    return <Snackbar
        key={+new Date()}
        layout="vertical"
        onClose={() => p.closeSnackbar}
        before={<Avatar size={24} style={p.good ? blueBackground : redBackground}>
            {p.good ? <Icon28DoneOutline fill="#fff" width={14} height={14} />
            :<Icon24Error fill="#fff" width={14} height={14}/>
            }

        </Avatar>}
    >
        {p.message}
    </Snackbar>
}

class DiversionPanel extends React.Component{
    diversionOffset = 60*10;
    constructor(props) {
        super(props);
        this.state = {
            snackbar: ''
        }
        this.diversion = this.diversion.bind(this);
        this.closeSnackbar = this.closeSnackbar.bind(this);
    }
    getInt(min, max) {
        return Math.floor(Math.random() * (max - min + 1) ) + min;
    }
    closeSnackbar(){
        this.setState({ snackbar: null })
    }
    diversion(e){
        let timeStamp = parseInt(+new Date/1000);
        let lastDiversion = this.props.user.lastDiversion;
        if(timeStamp - lastDiversion < this.diversionOffset){
            this.setState({
                snackbar: <NotifSnack closeSnackbar={this.closeSnackbar} good={false} message={`Диверсии можно проводить раз в 10 минут`}/>
            });
            return 0;
        }
        let q = this.props.areas;
        let w = q[this.props.curArea];
        // eslint-disable-next-line default-case
        switch (e) {
            case 0:
                if(this.getInt(0,1)){
                    let s = this.getInt(1,5);
                    w.happiness -= s;
                    this.setState({
                        snackbar: <NotifSnack closeSnackbar={this.closeSnackbar} good={true} message={`Уровень счастья снижен на ${s}`}/>
                    });
                }
                else{
                    this.setState({
                        snackbar: <NotifSnack closeSnackbar={this.closeSnackbar} good={false} message={`Диверсия провалилась`}/>
                    });
                }
                break;
        }
        if(w.happiness < 0){w.happiness = 0}
        q[this.props.curArea] = w;

        let z = this.props.users;
        let x = this.props.user;

        x.lastDiversion = parseInt(+new Date/1000);
        z['U'+getParams('vk_user_id')] = x;

        fire.firestore().collection('data').doc('areas').update({main: q}).then(() => {console.log('СОВЕРШЕНА ДИВЕРСИЯ')});
        fire.firestore().collection('data').doc('users').update({main: z}).then(() => {console.log('ОБНОВЛЕНЫ ДАННЫЕ ПОЛЬЗОВАТЕЛЯ')});
    }
    render() {
        let lastDiversion = this.props.user.lastDiversion*1000;
        let date = (new Date(this.props.user.lastDiversion*1000+this.diversionOffset*1000));
        return (
            <div>
                <PanelHeader
                    left={<PanelHeaderBack onClick={() => {this.props.openPanel('area')}}/>}
                >
                    Диверсии
                </PanelHeader>
                <Group description="диверсии могут снижать одобрение власти и уровень счастья, но и сами управляющие могут получить от них выгоду, списав свои неудачи на внешнее вмешательство.
                диверсии организуются за шв. франки. никто же не хочет оставлять следы.">
                    <SimpleCell style={date - lastDiversion <= this.diversionOffset*1000 ? {display: 'none'} : {}} before={<Icon24Error/>}>
                        {`Новая диверсия доступна в ${date.getHours()}:${date.getMinutes()}`}
                    </SimpleCell>
                    <SimpleCell
                        onClick={() => {this.diversion(0)}}
                        before={<Icon24Flash width={28} height={28}/>}
                        expandable={true}
                        description="20.000 шв. франков"
                    >Перекусить кабели</SimpleCell>
                </Group>
                {this.state.snackbar}
            </div>
        )
    }
}

export default DiversionPanel;